<template>
    <div id="login" class="small-dialog">
        <ul class="collection with-header dialog-contents">
            <li class="collection-header opaque-bg">
                <h4>Login</h4>
                <div class="row">
                    <form @submit.prevent="login" class="col s12">
                        <input 
                        type="text" 
                        v-model="email" 
                        placeholder="Email address" 
                        class="input" 
                        required>
                        <br/>
                        <input 
                        type="password" 
                        v-model="password"
                        placeholder="Password" 
                        class="input" 
                        required>
                        <br/><br>
                        <button v-on:click="login" class="btn btn-large bg-icon">Login</button>
                        <br><small ><router-link to="/register" class="view-icon">
                            New Here? Create an account
                        </router-link></small>
                    </form>
                </div>
            </li>
        </ul>
    </div>
</template>

<script>
import db from './firebaseinit'
import firebase from 'firebase'
export default {
    name: 'login',
    data () {
        return {
            email: null,
            password: null
        }
    },
    methods: {
        login() {
            firebase.auth().signInWithEmailAndPassword(this.email, this.password).then((user) => {
                alert("Login Successful")
                this.$router.replace('/')
            }).catch((err) => {
                alert("Invalid Username/Password")
            })
        }
    }
}
</script>


