<template>
    <div id="register-user" class="small-dialog">
        <ul class="collection with-header dialog-contents">
            <li class="collection-header opaque-bg">
                <h4>Register User</h4>
                <div class="row">
                    <form @submit.prevent="registerUser" class="col s12">
                        <div class="row no-margin">
                            <div class="input-field col s12">
                                <input type="text" v-model="user_name" required>
                                <label>Full Name</label>
                            </div>
                        </div>
                        <div class="row no-margin">
                            <div class="input-field col s12">
                                <input type="text" v-model="email" required>
                                <label>E-mail Address</label>
                            </div>
                        </div>
                        <div class="row no-margin">
                            <div class="input-field col s12">
                                <input type="text" v-model="password" required>
                                <label>Password</label>
                            </div>
                        </div>
                        <button type="submit" class="btn btn-large bg-icon">Register</button>
                        <br><small ><router-link to="/login" class="view-icon">
                            Already have an account? Login
                        </router-link></small>
                    </form>
                </div>
            </li>
        </ul>
    </div>
</template>

<script>
import db from './firebaseinit'
import firebase from 'firebase'
export default {
    name: 'register-user',
    data () {
        return {
            user_name: null,
            email: null,
            password: null
        }
    },
    methods: {
        registerUser() {
            firebase.auth().createUserWithEmailAndPassword(this.email, this.password).then((user) => {
            this.$router.replace('/login')
            }).catch((err) => {
                alert(err.message)
            });
        }
    }
}
</script>


