<template>
    <div id="dashboard">
        <div class="chip site-desc">
            Site Description: This site is built on VueJS 2 and Firestore for the database management. This sample application allows for
            CRUD Functionalities of "employees".
        </div>
        <ul class="collection with-header emp-top">
            <li class="collection-header opaque-bg">
                <h4 style="margin: 5px;">Employee List</h4>
            </li>
            <!-- bind with firebase id -->
            <li v-for="employee in employees" v-bind:key="employee.id" class="collection-item opaque-bg">
                <div class="chip">{{employee.dept}}</div>
                {{employee.employee_id}}:{{ employee.name }}
                
                <router-link class="secondary-content view-icon" v-bind:to="{name:'view-employee', params: {employee_id: employee.employee_id}}">
                    <i class="fa fa-eye"></i> View
                </router-link>
            </li>
        </ul>

        <div class="fixed-action-btn">
            <router-link to="/new" class="btn-floating btn-large bg-icon">
                <i class="fa fa-plus"></i>
            </router-link>
        </div>
    </div>
</template>

<script>
import firebase from 'firebase'
import db from './firebaseinit'
export default {
    name: 'dashboard',
    data () {
        return {
            employees: []
        }
    },
    created () {
        db.collection('employees').orderBy('dept').get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
                const data = {
                    'id': doc.id,
                    'employee_id': doc.data().employee_id,
                    'name': doc.data().name,
                    'dept': doc.data().dept,
                    'position': doc.data().position
                }
                this.employees.push(data)
            })
        })
    },
    methods: {
        logout () {
            firebase.auth().signOut().then(() => {
                this.$router.replace('login')
            })
        }
    }
}
</script>


