<template>
    <div id="view-employee">
        <ul class="collection with-header">
            <li class="collection-header opaque-bg">
                <h4>
                    {{name}}
                </h4>
            </li>
            <li class="collection-item opaque-bg">
                Employee ID#: {{employee_id}}
            </li>
            <li class="collection-item opaque-bg">
                Employee Department: {{dept}}
            </li>
            <li class="collection-item opaque-bg">
                Employee Position: {{position}}
            </li>
        </ul>
        <div class="view-options">
            <button @click="deleteEmployee" class="btn btn-large red">Delete</button>
            <router-link to="/" class="btn btn-large grey">Back</router-link>
        </div>
        
       
        <div class="fixed-action-btn">
            <router-link v-bind:to="{name: 'edit-employee', params: {employee_id: employee_id}}" class="btn-floating btn-large bg-icon">
                <i class="fa fa-pencil"></i>
            </router-link>
        </div>
    </div>
</template>

<script>
import db from './firebaseinit'
export default {
    name: 'view-employee',
    data () {
        return {
            employee_id: null,
            name: null,
            dept: null,
            position: null
        }
    },
    // navigation guard : before page loads
    beforeRouteEnter(to, from, next) {
        db.collection('employees').where('employee_id', '==', to.params.employee_id).get()
        .then(querySnapshot => {
            querySnapshot.forEach(doc => {
                next(vm => {
                    vm.employee_id = doc.data().employee_id
                    vm.name = doc.data().name
                    vm.dept = doc.data().dept
                    vm.position = doc.data().position
                })
            })
        })
    },
    watch: {
        '$route': 'fetchData'
    },
    methods: {
        fetchData() {
            db.collection('employees').where('employee_id', '==', this.$route.params.employee_id).get()
            .then(querySnapshot => {
                querySnapshot.forEach( doc => {
                    this.employee_id = doc.data().employee_id
                    this.name = doc.data().name
                    this.dept = doc.data().dept
                    this.position = doc.data().position
                })
            })
        },
        deleteEmployee() {
            if(confirm('Are you sure you want to delete this employee?')) {
                db.collection('employees').where('employee_id', '==', this.$route.params.employee_id).get()
                .then(querySnapshot => {
                    querySnapshot.forEach( doc => {
                        doc.ref.delete()
                        this.$router.push('/')
                    })
                })
            }
        }
    }
}
</script>


